@use 'sass:math';

.order {
    // .order__title
    &__title {
        margin-bottom: 46px;
        @media (max-width:$md4+px){
             margin-bottom: 32px; 
        }
    }
    // .order__contacts-order
    &__contacts-order {
        margin-bottom: 60px;
        @media (max-width:$md4+px){
             margin-bottom: 40px; 
        }
    }
     // .order__delivery-order
     &__delivery-order {
        margin-bottom: 60px;
        @media (max-width:$md4+px){
             margin-bottom: 40px; 
        }
    }
    // .order__info-bar
    &__info-bar {
        margin-bottom: 16px;
    }
}

.contacts-order {
    // .contacts-order__container
    &__container {
    }
    // .contacts-order__content
    &__content {
        display: flex;
        justify-content:space-between;
    }
    // .contacts-order__form
    &__form {
        flex: 0 1 480px;
        margin-right: 30px;
    }
    // .contacts-order__row
    &__row {
        margin-bottom: 24px;

        &_half-width {
            display: flex;
            align-items:center;
            @media (max-width:$md4+px){
                 flex-direction: column;
                 justify-content:center;
                 align-items: center; 
            }
        }
    }   
    // .contacts-order__title
    &__title {
        margin-bottom: 32px;

        font-size: 28px;
        line-height: math.div(36px,28px) * 100%;
        letter-spacing: math.div(.3px,28px) * 1em;
        font-weight: 700;
        @media (max-width:$md4+px){
             margin-bottom: 24px; 
    
             font-size: 16px;
        }
    }
    // .contacts-order__label
    &__label {
        display: block;
        margin-bottom: 6px;
        padding-left: 7px;

        font-size: 13px;
        line-height: math.div(21px,13px) * 100%;

        span {
            margin-right: 3px;

            font-size: 16px;
            color: var(--red)
        }
    }
    // .contacts-order__input
    &__input {
        textarea {
            width: 100%;
            height: 105px;
            resize: none;
            padding: 13px 16px;
            line-height: math.div(26px,16px) * 100%;
            font-weight: 600;
            color: var(--add-gray);

        }
    }
    // .contacts-order__column
    &__column {
        @media (max-width:$md4+px){
            width: 100%; 
        }
        &:not(:last-child){
            margin:0px 32px 0px 0px;
            @media (max-width:$md4+px){
                margin:0px 0px 16px 0px;
            }
        }
    }
    // .contacts-order__info
    &__info {
        flex: 0 0 288px;

        line-height: math.div(26px,16px) * 100%;

        @media (max-width:$md2+px){
            flex: 0 0 200px;
        }
        @media (max-width:$md3+px){
             display: none; 
        }

        &_contacts {
            padding-top: 27px;
        }

        p {
            &:not(:last-child){
                margin:0px 0px 16px 0px;
            }
        }
    }
}

//=====================Delivery Block================================
.delivery-order {
    // .delivery-order__container
    &__container {
    }
    // .delivery-order__content
    &__content {
        display: flex;
        justify-content:space-between;
    }
    // .delivery-order__items
    &__items {
        flex: 0 1 math.div(992,1344) * 100%;
        display: flex;
        flex-wrap: wrap;
        margin: 0 25px 0 -5px;

        @media (max-width:$md3+px){
             flex: 1 1 100%; 
             margin: 0 -5px;
        }
        @media (max-width:$md4+px){
             display: block; 
        }
    }
    // .delivery-order__item
    &__item {
        flex: 0 1 25%;

        @media (max-width:$md1+px){
             flex: 0 1 50%; 
        }
    }
    // .delivery-order__self-company
    &__self-company {
        max-width: 480px;
        margin-top: 48px;
        display: none;
        margin-bottom: 48px;

        @media (max-width:$md4+px){
             margin-top: 12px; 
        }
    }
}

.item-delivery {

    padding: 8px 4px;

    border: 2px solid transparent;
    border-radius: 6px;
    cursor: pointer;

    transition: border-color 0.3s ease 0s;

    @media (max-width:$md4+px){
         padding: 2px; 
    }

    &._active{
        border-color: var(--main-yellow);
    }
    &._inactive {
        cursor: no-drop;
        color: var(--add-gray);
    }
    // .item-delivery__body
    &__body {
        display: flex;
        flex-direction: column;
        justify-content:space-between;
        height: 100%;
        min-height: 243px;
        padding: 32px 16px 20px;

        border-radius: 4px;
        background-color: var(--light-gray);

        @media (max-width:$md4+px){
            min-height: 116px;
             padding: 16px 8px 32px; 
        }
    }
    // .item-delivery__top
    &__top {
        margin-bottom: 40px;

        @media (max-width:$md4+px){
            display: flex;
            justify-content:space-between;
             margin-bottom: 30px; 
        }
    }
    // .item-delivery__label
    &__label {
        margin-bottom: 16px;

        font-size: 19px;
        line-height: math.div(24px,19px) * 100%;
        letter-spacing: math.div(.3px,19px) * 1em;
        font-weight: 700;

        @media (max-width:$md4+px){
             margin-bottom: 0; 

             font-size: 16px;
        }
    }
    // .item-delivery__price
    &__price {
        line-height: math.div(26px,16px) * 100%;
        font-weight: 600;

        @media (max-width:$md4+px){
            display: flex;
            flex-direction: column;
            align-items: flex-end; 
        }
        
        &_choice {
            display: none;
            @media (max-width:$md4+px){
                 display: none; 
            }
            &._show {
                display: block;
            }
        }
    }
    // .item-delivery__info
    &__info {
        flex: 0 1 60px;

        font-size: 13px;
        line-height: math.div(21px,13px) * 100%;

        @media (max-width:$md4+px){
             flex: 0 1 auto; 
        }

        &_choice {
            display: none;
            &._show {
                display: block;
            }
        }
        // .item-delivery__info_by-self
        &_by-self {
            display: flex;
            align-items:center;

            font-size: 16px;
            line-height: math.div(26px,16px) * 100%;
            letter-spacing: math.div(.3px,16px) * 1em;
            font-weight: 800;
            color: var(--main-yellow);
            background-color: transparent;

            transition: color 0.3s ease 0s;

            @media (any-hover: hover){
                &:hover{
                    color: var(--hover-color);
                }
            }

            svg {
                margin-right: 12px;
            }
        }

    }
}

//=====================Info-Bar================================
.info-bar-order {

    @media (max-width:$md4+px){
         display: none; 
    }
    // .info-bar-order__list
    &__list {
        display: flex;
        justify-content: center;
        align-items:center;
    }
    // .info-bar-order__item
    &__item {
        position: relative;
        padding: 10px 0 12px;
        
        font-size: 13px;
        line-height: math.div(21px,13px) * 100%;
        font-weight: 600;
        opacity: .55;

        transition: opacity 0.3s ease 0s;

        &:not(:last-child){
            margin:0px 24px 0px 0px;
        }

        &._active{
            opacity: 1;

            &::after{
                background-color: var(--main-yellow);
            }
        }
        &::after{
            content:'';
            position: absolute;
            bottom: 0;
            left: 50%;
            transform: translate(-50%,0px);

            width: 32px;
            height: 4px;

            border-radius: 2px;
            background-color: transparent;

            transition: background-color 0.3s ease 0s;
        }
    }
}




