@use 'sass:math';

.order-placed {
    // .order-placed__details
    &__details {
    }
     // .order-placed__what-next
     &__what-next {
        margin-bottom: 70px;

        @media (max-width:$md4+px){
             margin-bottom: 80px; 
        }
    }
}
.details-placed {
    // .details-placed__container
    &__container {
    }
    // .details-placed__title
    &__title {
        text-align: left;
    }
    // .details-placed__content
    &__content {
        max-width: 992px;
    }
    // .details-placed__descroption
    &__descroption {
        max-width: 560px;
        margin-bottom: 22px;

        line-height: math.div(26px,16px) * 100%;
    }
    // .details-placed__pay-type
    &__pay-type {
        margin-bottom: 32px;
        
    }
    // .details-placed__delivery
    &__delivery {
        display: flex;
        margin-bottom: 48px;
        @media (max-width:$md4+px){
            display: block;
             margin-bottom: 32px; 
        }
    }
    // .details-placed__item
    &__item {
        flex: 0 1 236px;
        &:not(:last-child){
            margin:0px 16px 0px 0px;

            @media (max-width:$md4+px){
                margin:0px 0px 16px 0px;
            }
        }
    }
    // .details-placed__requisites
    &__requisites {
        margin-bottom: 48px;
        @media (max-width:$md4+px){
             margin-bottom: 32px; 
        }
    }
    // .details-placed__goods
    &__goods {
        display: flex;
        justify-content:space-between;
        padding-top: 8px;
        margin-bottom: 48px;

        border-top: 1px solid rgba(59, 59, 59, 0.1);
    
        
        @media (max-width:$md3+px){
            padding-top: 0;
            flex-direction: column;
        }
        @media (max-width:$md4+px){
            margin-bottom: 40px;

            border-top: none; 
       }
    }
    // .details-placed__items
    &__items {
        padding-top: 24px;
        flex: 0 1 math.div(740,992) * 100%;
        @media (max-width:$md3+px){
             flex: 0 1 auto; 
             padding-top: 0;
        }
    }
    // .details-placed__row
    &__row {
        font-size: 13px;
        line-height: math.div(21px,13px) * 100%;

        &:not(:last-child){
            margin:0px 0px 4px 0px;
        }
    }
    // .details-placed__right-column
    &__right-column {
        flex: 1 1 auto;
        justify-content: flex-end;
    }
   
}

.pay-type {

    @media (max-width:$md4+px){
         display: flex;
         align-items: center;
         justify-content:space-between; 
    }
    // .pay-type__label
    &__label {
        margin-bottom: 4px;

        line-height: math.div(26px,16px) * 100%;

        &_card {
            display: flex;
            align-items:center;
            color: var(--ligther-green);

            font-size: 13px;
        }
    }
    // .pay-type__cifres
    &__cifres {
        font-size: 28px;
        line-height: math.div(36px,28px) * 100%;
        letter-spacing: math.div(.3px,28px) * 1em;
        font-weight: 700;

        @media (max-width:$md4+px){
             font-size: 24px; 
        }
    }
    // .pay-type__icon
    &__icon {
        margin-right: 8px;
    }
}
.item-details {
    display: flex;
    flex-direction: column;
    justify-content:space-between;
    min-height: 217px;
    padding: 32px 16px 30px;

    border: 1px solid #EDEDED;

    @media (max-width:$md4+px){
        min-height: 116px;
         padding: 16px 8 32px; 
    }
    // .item-details__top
    &__top {
        @media (max-width:$md4+px){
             display: flex;
             align-items:center;
             justify-content:space-between; 
        }
    }
    // .item-details__label
    &__label {
        margin-bottom: 16px;

        font-size: 19px;
        line-height: math.div(24px,19px) * 100%;
        letter-spacing: math.div(.3px,19px) * 1em;
        font-weight: 700;

        @media (max-width:$md4+px){
            margin: 0;
             font-size: 16px; 
        }
    }
    // .item-details__cifres
    &__cifres {
        font-weight: 600;
        line-height: math.div(26px,16px) * 100%;
    }
    // .item-details__info
    &__info {
        flex: 0 1 70px;

        font-size: 13px;
        line-height: math.div(21px,13px) * 100%;

        @media (max-width:$md4+px){
            flex: 0 1 auto;
             font-size: 10px; 
        }
    }
}
.requisites-details {
    display: flex;
    justify-content:space-between;

    border-top: 1px solid rgba(59, 59, 59, 0.1);
    
    @media (max-width:$md3+px){
         flex-direction: column; 
    }
    @media (max-width:$md4+px){
         border-top: none; 
    }
    // .requisites-details__title
    &__title {
        flex: 0 0 177px;
        padding-top: 8px;
        margin-right: 30px;

        font-size: 16px;
        line-height: math.div(26px,16px) * 100%;
        font-weight: 600;

        @media (max-width:$md3+px){
             flex: 0 1 auto; 
             padding: 0;
             margin: 0px 0px 16px 0px;
        }
    }
    // .requisites-details__rows
    &__rows {
        flex: 0 1 math.div(740,992) * 100%;
        @media (max-width:$md3+px){
             flex: 0 1 auto; 
        }
    }
    // .requisites-details__row
    &__row {
        display: flex;
        align-items:center;
        justify-content:space-between;
        padding: 12px 32px;

        &:nth-child(even) {
            background-color: rgba(59, 59, 59, 0.05);

            @media (max-width:$md4+px){
                 background-color: inherit; 
            }
        }

        @media (max-width:$md3+px){
             padding: 12px 0; 
        }
        @media (max-width:$md4+px){
            flex-direction: column;
            align-items: flex-start;
            margin: 0 -15px; 
            padding: 0;
        }
    }
    // .requisites-details__label
    &__label {
        font-size: 13px;
        line-height: math.div(21px,13px) * 100%;
        color: rgba(17, 17, 17, 0.45);

        @media (max-width:$md4+px){
            width: 100%;
             padding: 12px 15px; 
        }
    }
    // .requisites-details__cifres
    &__cifres {
        flex: 0 1 322px;
        font-size: 13px;
        line-height: math.div(21px,13px) * 100%;
        @media (max-width:$md2+px){
            flex: 0 1 auto;
        }
        @media (max-width:$md4+px){
            width: 100%;
            padding: 12px 15px;
            background-color: var(--light-gray); 
        }
    }
}


.what-next {
    // .what-next__container
    &__container {
    }
    // .what-next__content
		&__content {
            max-width: 992px;
		}
    // .what-next__title
    &__title {
        margin-bottom: 40px;
        font-size: 28px;
        line-height: math.div(26px,28px) * 100%;
        font-weight: 700;
        letter-spacing: math.div(.3px,28px) * 1em;

        @media (max-width:$md4+px){
             margin-bottom: 24px; 
             font-size: 19px;
        }
    }
    // .what-next__items
    &__items {
        counter-reset: item;
    }
    // .what-next__item
    &__item {


        &:not(:last-child){
            margin:0px 0px 40px 0px;

            @media (max-width:$md4+px){
                 margin-bottom: 32px; 
            }
        }
        &::before{
            counter-increment: item;
            content: counter(item);

            display: flex;
            justify-content:center;
            align-items: center;
            width: 48px;
            height: 48px;
            margin-bottom: 24px;

            font-size: 22px;
            line-height: math.div(28px,22px) * 100%;
            font-weight: 700;
            color: var(--main-white);

            border-radius: 50%;
            background-color: var(--main-yellow);

            @media (max-width:$md4+px){
                 margin-bottom: 16px;
            }
        }
    }
    // .what-next__label
    &__label {
        margin-bottom: 10px;

        font-size: 19px;
        line-height: math.div(24px,19px) * 100%;
        letter-spacing: math.div(.3px,19px) * 1em;
        font-weight: 700;

        @media (max-width:$md4+px){
             margin-bottom: 8px;
             
             font-size: 16px;
        }
    }
    // .what-next__text
    &__text {
        max-width: 608px;
        line-height: math.div(26px,16px) * 100%;

        p {
            &:not(:last-child){
                margin:0px 0px 16px 0px;

                @media (max-width:$md4+px){
                     margin-bottom: 8px; 
                }
            }
        }
    }
}




