@use 'sass:math';
.town-choise {
    position: fixed;
    z-index: 90;
    bottom: -300px;
    right: 142px;

    width: 480px;
    padding: 16px 16px 20px;

    background-color: #fff;
    border: 1px solid #DFDFDF;

    box-shadow: 0px 6px 10px rgba(60, 61, 105, 0.1);
    border-radius: 6px;

    // transform: translate(0px,200px);

    transition: bottom 1s ease 0s;

    @media (max-width:$md4+px){
         width: 90%;
         right: 0;
         left: 15px;
         padding: 8px 16px;
    }

    &._active{
        bottom: 118px;
    }
    // .town-choise__close
    &__close {
        position: absolute;
        top: 12px;
        right: 12px;

        background-color: transparent;
    }
    // .town-choise__label
    &__label {
        margin-bottom: 28px;

        font-size: 19px;
        line-height: math.div(32px,19px) * 100%;
        font-weight: 600;

        @media (max-width:$md4+px){
             font-size: 16px;
             margin-bottom: 16px; 
        }
    }
    // .town-choise__buttons
    &__buttons {
        display: flex;
    }
    // .town-choise__btn
    &__btn {
        padding: 13px 52.5px;

        line-height: math.div(26px,16px) * 100%;
        letter-spacing: math.div(.3px,16px) * 1em;
        font-weight: 800;

        transition: background-color 0.3s ease 0s,
                    color 0.3s ease 0s;

        @media (max-width:$md4+px){
            padding: 7px 38.5px;
             font-size: 16px; 
        }
        // .town-choise__btn_yes
        &_yes {
            background-color: var(--main-yellow);
            color: #fff;

            @media (any-hover: hover){
                &:hover{
                    background-color: var(--hover-color);
                }
            }
            @media (max-width:$md4+px){
                 margin-bottom: 15px; 
            }
        }
        // .town-choise__btn_no
        &_no {
            color: var(--main-yellow);
            background-color: #fff;

            @media (any-hover: hover){
                &:hover{
                    color: var(--hover-color);
                }
            }
        }
    }
}
